import React from "react";
import { OyeHeader } from "./OyeHeader";
import { OyeFooter } from "./OyeFooter";
import { OyeInput } from "./OyeInput";
import { useState } from "react";
import { OyeVerificaPass2 } from "./OyeVerificaPass2";

function OyeRegistro() {
  const [usuario, cambiarUsuario] = useState({ campo: "", valido: null });
  const [password, cambiarPassword] = useState({ campo: "", valido: null });
  const [password2, cambiarPassword2] = useState({ campo: "", valido: null });
  const [edad, setEdad] = useState("");
  const [terminos, setTerminos] = useState(false);
  const [formularioValido, setFormularioValido] = useState(null);

  const expresiones = {
    password: /^(?=.*\d)(?=.*[a-zA-Z]).{8,}$/, // min 8 caracteres, debe contener un número, debe contener una letra.
    correo: /^(([^<>()[\]\\.,;:\s@”]+(\.[^<>()[\]\\.,;:\s@”]+)*)|(“.+”))@((\[[0–9]{1,3}\.[0–9]{1,3}\.[0–9]{1,3}\.[0–9]{1,3}])|(([a-zA-Z\-0–9]+\.)+[a-zA-Z]{2,}))$/
  };

  const cambiarEdad = (e) => {
    setEdad(e.target.value);
  };

  //valida password 2
  const validarPassword2 = () => {
    if (password2.campo.length > 0) {
      if (password.campo.length > 0) {
        if (password.campo !== password2.campo) {
          cambiarPassword2({ ...password2, valido: "false" });
        } else {
          cambiarPassword2({ ...password2, valido: "true" });
          console.log(password2.valido);
        }
      }
    } else {
      cambiarPassword2({ ...password2, valido: null });
    }
  };

  const cambiarTerminos = (e) => {
    setTerminos(e.target.checked);
  };

  onsubmit = (e) => {
    e.preventDefault();

    if (
      usuario.valido === "true" &&
      password.valido === "true" &&
      password2.valido === "true" &&
      edad !== "" &&
      terminos
    ) {
      setFormularioValido(true);
      cambiarUsuario({ campo: "", valido: "" });
      cambiarPassword({ campo: "", valido: null });
      cambiarPassword2({ campo: "", valido: null });
      console.log("enviado");
      // ...
    } else {
      setFormularioValido(false);
      console.log("para boludo");
    }
  };

  return (
    <React.Fragment>
      <OyeHeader />
      <main>
        <div className="py-5">
          <div className="col-sm-9 col-lg-5 mx-auto px-2">
            <form className="text-white" id="formulario">
              <OyeInput
                estado={usuario}
                cambiarEstado={cambiarUsuario}
                tipo="email"
                label="Correo Electronico"
                placeholder="abc@ejemplo.com"
                name="correo"
                leyendaError="El correo electrónico debe tener un @ y un dominio"
                expresionRegular={expresiones.correo}
              />
              <OyeInput
                estado={password}
                cambiarEstado={cambiarPassword}
                tipo="password"
                label="Contraseña"
                name="contraseña"
                leyendaError="La contraseña debe contener mínimo 8 caracteres y un número"
                expresionRegular={expresiones.password}
              />
              <OyeVerificaPass2
                estado={password2}
                cambiarEstado={cambiarPassword2}
                tipo="password"
                label="Verificar Contraseña"
                name="contraseña2"
                leyendaError="Las contraseñas deben coincidir"
                funcion={validarPassword2}
              />

              <div className="mb-3">
                <label className="form-label">Edad *</label>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="flexRadioDefault"
                    id="menor_20"
                    value="menor a 20"
                    onChange={cambiarEdad}
                  ></input>
                  <label className="form-check-label">Menor de 20</label>
                </div>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="flexRadioDefault"
                    id="20_40"
                    value="20 a 40"
                    onChange={cambiarEdad}
                  ></input>
                  <label className="form-check-label">20 a 40</label>
                </div>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="flexRadioDefault"
                    id="mayor_50"
                    value="50"
                    onChange={cambiarEdad}
                  ></input>
                  <label className="form-check-label">Mayor a 50</label>
                </div>
              </div>
              <div className="mb-3 form-check">
                <input
                  type="checkbox"
                  className="form-check-input"
                  id="exampleCheck1"
                  checked={terminos}
                  onChange={cambiarTerminos}
                ></input>
                <label className="form-check-label">
                  Acepto los terminos de uso *
                </label>
              </div>
              <p id="campos_obligatorios">* Campos obligatorios</p>
              <button type="submit" className="btn btn-success">
                Submit
              </button>
            </form>
          </div>
        </div>
      </main>
      <OyeFooter />
    </React.Fragment>
  );
}

export { OyeRegistro };
