import React from "react";
import { useState } from "react";
import { OyeBuscador } from "./OyeBuscador";
import { OyeApi } from "./OyeApi";
import { OyeHeader } from "./OyeHeader";
import { OyeFooter } from "./OyeFooter";

const defaultCanciones = [
  {
    nombre: "vive",
    ruta: "./canciones/uno.mp3",
    reproducciones: 10,
    icono: 4
  },
  {
    nombre: "invierno",
    ruta: "./canciones/dos.mp3",
    reproducciones: 30,
    icono: 1
  },
  {
    nombre: "planeta",
    ruta: "./canciones/tres.mp3",
    reproducciones: 20,
    icono: 3
  },
  {
    nombre: "suave",
    ruta: "./canciones/cuatro.mp3",
    reproducciones: 80,
    icono: 2
  }
];
// console.log(canciones);

function OyeMain() {
  const [canciones, setCanciones] = useState(defaultCanciones);
  const [buscar, setBuscar] = React.useState("");

  let searchedCanciones = [];

  if (!buscar.length >= 1) {
    searchedCanciones = canciones;
  } else {
    searchedCanciones = canciones.filter((canciones) => {
      const nombresCanciones = canciones.nombre.toLocaleLowerCase();
      const buscarMin = buscar.toLocaleLowerCase();
      return nombresCanciones.includes(buscarMin);
    });
  }
  // console.log(nombresCanciones);
  return (
    <React.Fragment>
      <OyeHeader />
      <main>
        <div className="py-5">
          <div className="col-sm-9 mx-auto">
            <OyeBuscador valor={buscar} setBuscar={setBuscar} />
            <div className="d-flex flex-wrap justify-content-center">
              {searchedCanciones.map((canciones) => (
                <OyeApi
                  key={canciones.nombre}
                  nombre={canciones.nombre}
                  ruta={canciones.ruta}
                />
              ))}
            </div>
          </div>
        </div>
      </main>
      <OyeFooter />
    </React.Fragment>
  );
}

export { OyeMain };
