import React from 'react';
import { OyeHeader } from './OyeHeader'
import ImgPortada from './imagenes/portada.jpeg'
import { OyeFooter } from './OyeFooter';
import { NavLink } from 'react-router-dom';

function OyeInicio (){
    return(
        <React.Fragment>
            <OyeHeader />
        <main>      
        <div className="mt-3 p-3 col-sm-9 mx-auto text-white ">
            
          <div className="row shadow" id="box-inicio">
            <div className="col-12 col-md-6 mx-auto pt-4 p-4">
              <h2>Caciones Gratis para la comunidad</h2>
              <p>Con este sitio podrás escuchar audios de manera libre...</p>
              <NavLink to="/OyeCanciones">
              <button type="button" className="btn btn-warning">Ver Canciones</button>
              </NavLink>
             

            </div>
            <div className="col-12 col-md-6 mx-auto p-4">
              <div className="d-flex flex-wrap justify-content-center">
                <img src={ImgPortada} className="img-fluid mx-auto" alt="picture"></img> 
              </div>                
            </div>
          </div>

          <hr></hr>

          <div className="row mb-3">
            <div className="col">
              <table className="table table-light">                  
                <thead>
                  <tr>
                    <th colSpan={2}>
                      <h2 className="text-center">TOP 3</h2>
                    </th>
                  </tr>                                   
                </thead>
                
                <tbody>
                  <tr>
                    <th>Nombre</th>
                    <th>Canción</th>   
                  </tr>
                  <tr>
                    <td>Vive</td>
                    <td>
                      <audio src="./canciones/ocho.mp3" controls></audio>
                    </td>
                  </tr>
                  <tr>
                    <td>Primavera</td>
                    <td>
                      <audio src="./canciones/dos.mp3" controls></audio>
                    </td>
                  </tr>
                  <tr>
                    <th>Hoy</th>
                    <td>
                      <audio src="./canciones/cinco.mp3" controls></audio>
                    </td>                    
                  </tr>
                </tbody>
              </table>
            </div>
          </div> 
                
        </div>      
    </main>
    <OyeFooter />
    </React.Fragment>
    );
}

export { OyeInicio };