import React from "react";

import { useState } from "react";
// import { OyeRegistro } from './OyeRegistro';

function OyeInput ({estado, cambiarEstado, tipo, label, placeholder, name, leyendaError, expresionRegular, funcion}){


  
  const [is_valid, setIs_valid] = useState('');
  const [ayuda, setAyuda] = useState('no_ayudar');
  
  const onChange = (e) => {
		cambiarEstado({...estado, campo: e.target.value});
    // console.log(estado.campo)
    if(e.target.value !== "" && estado.valido === 'true'){
      setIs_valid("is-valid");
      setAyuda("no_ayudar");
    }else if(e.target.value === ""){
      setIs_valid("");
      setAyuda("no_ayudar");
    }else{
      setIs_valid("is-invalid");
      setAyuda("ayudar")
    }    
	}

  const validacion = () => {
		if(expresionRegular){
			if(expresionRegular.test(estado.campo)){
				cambiarEstado({...estado, valido : 'true'});
			} else {
				cambiarEstado({...estado, valido : 'false'});
			}
		}
    if(funcion){
			funcion();
		}
	}
  return (
    <div className="mb-3">
      <label className="form-label" htmlFor={name}>{label}</label>      
      <input 
      type={tipo}
      placeholder={placeholder}
      value={estado.campo}
      className={`form-control ${is_valid}`}
      id={name}
      onChange={onChange}
      onKeyUp={validacion}
      onBlur={validacion}
      valido={estado.valido}

      
      />
      <div id={ayuda} className="form-text">{leyendaError}</div>
    </div>
  );
}

export { OyeInput };