import React from 'react';
import { OyeHeader } from './OyeHeader';
import { OyeFooter } from './OyeFooter';

function OyeLogin (){
    return(
        <React.Fragment>
            <OyeHeader />
            <main>
                <div className="py-5">
                    <div className="col-sm-6 col-lg-4 mx-auto px-2 text-white">
                    <h2 className="text-center">Iniciar Sesión</h2>
                    <hr></hr>
                    <form>
                        <div className="mb-3">
                        <label className="form-label">Correo electronico</label>
                        <input type="email" className="form-control" id="InputEmail1" aria-describedby="emailHelp"></input>
                        <div id="emailHelp" className="form-text">Aqui va el texto de ayuda</div>
                        </div>
                        <div className="mb-3">
                        <label className="form-label">Contraseña</label>
                        <input type="password" className="form-control" id="Password1"></input>
                        <div id="emailHelp" className="form-text">Aqui va el texto de ayuda</div>
                        </div>
                        <button type="submit" className="btn btn-success">Submit</button>
                    </form>
                    </div>
                </div>
            </main>
            <OyeFooter />
        </React.Fragment>
    );
}

export { OyeLogin };